
:root{
    --PrimaryColor: hsl(199,100%,33%);
    --SecondaryColor: hsl(187,85%,43%);
    --gradientColor: linear-gradient(to right, hsl(187,85%,43%),hsl(199,100%,33%));
    --whiteColor:hsl(0,0%,100%);
    --blackColor:hsl(201,33%,16%);
    --textColor:hsl(240 ,4% ,36%);
    --WhiteColorDeam:hsl(0,0%,93%);
    --greyText:rgb(190,190,190);
    --inputColor:rgb(239,239,239);
    --bodyColor: rgb(240,240,246);
    --cardBG:rgb( 225,225,235);
  }
  .home{
    width: 100%;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: auto;
    .overlay{
        position: absolute;
        width: 100%;
        height: 100%;
        background: rgb(47,106, 127);
        top: 0;
        bottom: 0;
        z-index: 1;
        mix-blend-mode:hard-light;
    }
    video{
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        bottom: 0;
        object-fit: cover;
    }
    .homecontent{
        width: 100%;
        height: max-content;
        padding: 8rem 1.5rem 3rem;
        display: flex;
        row-gap: 3rem;
        flex-direction: column;
        align-items: stretch;
        justify-content: center;
        margin: auto;
        color: var(--whiteColor);
        z-index: 100;
        .textDiv{
            padding: 2rem 0;
            .smalltext{
                font-size: 13px;
                text-transform: uppercase;
            }
            .homeTitle{
                position: relative;
                font-size: 1.8rem;
                width: 180px;
            }
        }
        .cardDiv{
            background: var(--whiteColor);
            position: relative;
            padding: 2rem 1rem ;
            border-radius: 10px;

            label{
                display: block;
                color: var(--textColor);
                padding-bottom: 10px;
                font-size: 13px;
                font-weight: 500;
            }
            .label_total{
                justify-content: space-between;
                align-items: center;
                .total{
                    color: var(--textColor);
                    font-size: 13px;
                    padding-bottom: 10px;
                }
            }
            .input{
                height: 40px;

                padding: 0 1rem;
                background: var(--inputColor);
                border-radius: 3rem;
                input{
                    outline: none;
                    background-color: none;
                    border: none;
                    width: 100%;
                    font-size: 13px;
                    font-weight: 500;
                    &::placeholder{
                        font-size: 13px;
                    }
               
                
                   
                }  
                .location{
                    color: var(--PrimaryColor);
                }  
            }
            .searchoption{
                position: absolute;
                bottom: -0.7rem;
                background: var(--gradientColor);
                width: max-content;
                column-gap: .8rem;
                padding: 5px 1rem;
                border-radius: 3rem;
                justify-content: center;
                left: 50%;
                cursor: pointer;
                transform: translate(-50%);
                &:hover{
                    background: var(--PrimaryColor);
                }
                .icon{
                    font-size: 18px;
                }
                span{
                    font-size: 13px;
                }
            }

        }
        .homefootericon{
            width: 100%;
            justify-content: space-between;
            .icon{
                font-size: 20px;
                margin: 0 5px;
                color: var(--whiteColor);
                font-weight: 800;
                &:hover{
                    color: var(--SecondaryColor);
                }
            }
        }
    }


  }
  /* media queries============================ */
  @media screen and (min-width:500px) {
    .home{
        .homecontent{
            .textDiv{
                .smallText{
                    font-size: 16px;
                }
                .homeTitle{
                    position: relative;
                    width: max-content;
                    font-size:2.3rem ;
                    &::after{
                        position: absolute;
                        content: '';
                        background: var(--SecondaryColor);
                        height: 5px;
                        width: 165px;
                        bottom: 5px;
                        right: 0;
                        z-index: -1;
                    }
                }
            }
            .cardDiv{
                grid-template-columns: repeat(2,1fr);
            }
        }
    }
  }

  @media screen and (min-width:660px) {
    .home{
        .homecontent{
            .cardDiv{
                padding: 2rem 1rem;
                grid-template-columns: repeat(3,1fr);
            }
        }
    }
  }
  @media screen and (min-width:940px) {
    .home{
        height: 80vh;
    }
  }
  @media screen and (min-width:1240px) {
    .home{
        height: 90vh;
        .homecontent{
            padding-left: 0;
            padding-right: 0;
            .cardDiv{
                padding: 2rem 1rem;
                .input{
                    margin-bottom: 1rem;
                }
                .searchoption{
                    padding: 8px 1rem;
                    bottom: -1rem;
                span{
                    font-size:17px
                }

                }
            }
        }
    }
  }