
:root{
    --PrimaryColor: hsl(199,100%,33%);
    --SecondaryColor: hsl(187,85%,43%);
    --gradientColor: linear-gradient(to right, hsl(187,85%,43%),hsl(199,100%,33%));
    --whiteColor:hsl(0,0%,100%);
    --blackColor:hsl(201,33%,16%);
    --textColor:hsl(240 ,4% ,36%);
    --WhiteColorDeam:hsl(0,0%,93%);
    --greyText:rgb(190,190,190);
    --inputColor:rgb(239,239,239);
    --bodyColor: rgb(240,240,246);
    --cardBG:rgb( 225,225,235);
  }
  .header{
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: var(--WhiteColorDeam);
    padding: 1rem;
    width: 100%;
    z-index: 1000;
    box-shadow: 0 2px 4px rgba(2, 15, 29, 0.9);

    .logo{
        color: var(--blackColor);
        font-weight: 600;
        cursor: pointer;
        align-items: center;
        justify-content: center;
        .icon{
            font-size: 25px;
            transform: translateY(5px);
            color: var(--PrimaryColor);
        }
    }
    @media screen and (max-width:768px){
        .navbar{
            position: absolute;
            background: var(--WhiteColorDeam);
            height: max-content;
            width: 80%;
            border-radius: 1rem;
            top: -500px;
            padding: 1rem;
            left: 50%;
            transform: translate(-50%);
            z-index: 2000;
            box-shadow: 0 2px 4px rgba(2, 15, 29, 0.9);
            transition: .5s ease-in-out;
        
            .navbarlist{
                flex-direction: column;
                justify-content: center;
                width: 100%;
                margin: auto;
                padding: .5rem 0;
                .navbaritem{
                    padding: .5rem 0;
                }
                .navlink{
                    color: var(--textColor);
                    font-size: .9rem;
                    font-weight: 600;
                    &:hover{
                        color: var(--PrimaryColor);
                    }
                }
            }
            .btn{
                margin-top: 1rem;
                a{
                    font-weight: 600;
                    color: var(--whiteColor);
                }
            }
            .closeNavbar{
                position: absolute;
                top: 1rem;
                right: 1.5rem;
                color: var(--PrimaryColor);
                &:hover{
                    color: var(--SecondaryColor);
                }
            }
        }
        .activNavbar{
            top: 7rem;
        }
    }
    .togglenavbar{
        .icon{
            font-size: 25px;
            color: var(--PrimaryColor);
            &:hover{
                color: var(--SecondaryColor);
            }
        }
    }
  }
  /* Media Queries================================== */
   @media screen and (min-width:769px) {
    .togglenavbar,.closeNavbar{
        display: none;
        
    }
    .header{
        padding: 1.5rem 2rem;
        .navbar{
            .navbarlist{
                display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: row;
            }
            .navbaritem{
                .navlink{
                    color: var(--textColor);
                    padding: 0 0.45rem;
                    font-size: 0.9rem;
                    font-weight: 500;
                    &:hover{
                        color: var(--PrimaryColor);
                    }

                }
            }
            .btn{
                margin-left: 1rem;
                a{
                    color: var(--whiteColor);
                    font-weight: 500;
                }
            }
        }
    }
    
 } 

 @media screen and (min-width:960px) {
    .header{
     .navbar{
        .navbarlist{
            .navbaritem{
                .navlink{

                    padding: 0 0.7rem;
                }
            }
        }
     }   
        
    }
 }
 @media screen and (min-width:1240px) {
    .header{
     padding: 1rem 8.5rem; 
        
    }
 }
